import { Button } from '@material-ui/core'
import TextField from '@material-ui/core/TextField'
import * as React from 'react'

import { pushHistory } from '@src/history'
import { UrlPathEnum } from '@src/types'
import { ButlerContext } from '@src/Root/contexts'

export const LoginPage = props => {
  const butler = React.useContext(ButlerContext)
  const [username, setUsername] = React.useState('')
  const [password, setPassword] = React.useState('')
  const submitLogin = () => {
    butler.AuthenticateUser(
      {
        username: username,
        password: password,
      },
      'replace',
      data => {
        sessionStorage.setItem('authToken', data.authToken)
        // Redirect to root
        pushHistory(UrlPathEnum.Root)
      },
    )
  }
  return (
    <div
      style={{
        height: `${window.innerHeight}px`,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
      }}
    >
      <TextField label="Username" onChange={e => setUsername(e.target.value)} />
      <br />
      <TextField
        onChange={e => setPassword(e.target.value)}
        type="password"
        label="Password"
        onKeyPress={e => {
          if (e.charCode == 13) {
            submitLogin()
          }
        }}
      />
      <br />
      <Button variant="contained" color="primary" onClick={() => submitLogin()}>
        Login
      </Button>
    </div>
  )
}
