import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { BUTLER_ACTIONS } from '@src/__generated__/backendSchemas'

export function stringToDate(dateString: string): Date {
  const [year, month, day] = dateString.split('-').map(v => parseInt(v))
  const ret = new Date(year, month - 1, day)
  return ret
}
export function formatDate(d: string): string {
  const date = new Date(d)
  return date.toLocaleDateString('en-US', {
    month: 'short',
    day: 'numeric',
    timeZone: 'UTC',
  })
}

export const getIsoDateString = (d: Date) => {
  const year = d.getFullYear()
  const month = d.getMonth() < 9 ? '0' + (d.getMonth() + 1) : d.getMonth() + 1
  const day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate()
  return `${year}-${month}-${day}`
}

export function getToday(): string {
  const today = new Date()
  const year = today.getFullYear()
  const month = today.getMonth() < 9 ? '0' + (today.getMonth() + 1) : today.getMonth() + 1
  const day = today.getDate() < 10 ? '0' + today.getDate() : today.getDate()
  return `${year}-${month}-${day}`
}

export interface Identifiable {
  id: string | number
}
export const mergeArrays = <T extends Identifiable>(
  existing: Array<T>,
  incoming: Array<T>,
  prependOrAppend: 'prepend' | 'append',
): Array<T> => {
  return incoming.reduce(
    function(accumulator: Array<T>, value: T) {
      const index = existing.findIndex(x => x.id === value.id)
      if (index >= 0) {
        const ret = [
          ...accumulator.slice(0, index),
          value,
          ...accumulator.slice(index + 1, accumulator.length),
        ]
        return ret
      } else {
        return prependOrAppend === 'append' ? [...accumulator, value] : [value, ...accumulator]
      }
    },
    [...existing],
  )
}
export interface ButlerProps {
  butler: typeof BUTLER_ACTIONS
}
const mapDispatchToProps = (dispatch): ButlerProps => {
  return {
    butler: bindActionCreators(BUTLER_ACTIONS, dispatch),
  }
}

export const connectButler = <T extends {}>(stateMapper: ((state) => T) | null) => {
  return connect<T, ButlerProps>(
    stateMapper,
    mapDispatchToProps,
  )
}
