import {
  ExpansionPanel,
  ExpansionPanelDetails,
  ExpansionPanelSummary,
  Fab,
  Grid,
  Icon,
  Tooltip,
  Typography,
} from '@material-ui/core'
import { Delete } from '@material-ui/icons'
import * as _ from 'lodash'
import JSONTree from 'react-json-tree'
import * as React from 'react'
import { getSelectableCategories } from '../selectors'
import { ButlerContext } from '@src/Root/contexts'
import { CategoryField, SmartCatField } from '@src/components/CategoryField'
import { DebouncedTextField } from '@src/components/DebouncedTextField'
import { ICategory, IUserTransaction } from '@src/__generated__/backendSchemas'
import { formatDate, connectButler } from '@src/utils'
import { PlaidIcon } from '@src/components/icons'

interface ITransactionRowProps {
  transaction: IUserTransaction
  user_id: number
  expanded: boolean
  selectableCategories: Array<ICategory>
  onToggleExpansion: (event, expanded) => void
}

export const CategoryInfoField = (props: {
  transaction: IUserTransaction
  category: ICategory | null
  user_id: number
  onAccept: (arg: { id: number; category_id: number; balance_adjust: string }) => void
}) => {
  const { transaction, category, onAccept } = props
  const prediction = transaction.prediction
  const multiplier = props.user_id === transaction.lender_id ? 1 : -1
  if (prediction) {
    return (
      <SmartCatField
        prediction={prediction}
        onAccept={() =>
          onAccept({
            id: transaction.id,
            category_id: prediction.category_id,
            balance_adjust: prediction.split
              ? ((multiplier * parseFloat(transaction.total_amount)) / 2).toString()
              : '0',
          })
        }
      />
    )
  }
  return (
    <Grid container spacing={10}>
      <Grid item xs={1}>
        <Icon className="material-icons">{category ? category.mui_icon_id : null}</Icon>
      </Grid>
      <Grid item xs>
        {category ? <Typography color="textSecondary">{category.description}</Typography> : null}
      </Grid>
    </Grid>
  )
}

const TransactionRowComponent = (props: ITransactionRowProps) => {
  const { user_id, selectableCategories, transaction, expanded, onToggleExpansion } = props
  const butler = React.useContext(ButlerContext)
  console.log('render row')

  const onAcceptSmartCat = (fields: {
    id: number
    category_id: number
    balance_adjust: string
  }) => {
    butler.UpdateTransaction({ ...fields }, 'merge-append', () =>
      fields.balance_adjust ? butler.GetUserBalance({}, 'replace') : null,
    )
  }
  const getCategory = () => {
    if (!transaction.category_id) return null
    return selectableCategories.find(c => c.id === transaction.category_id) || null
  }
  const [balanceAdjust, setBalanceAdjust] = React.useState(transaction.balance_adjust)
  return (
    <div>
      <ExpansionPanel
        disabled={!transaction.editable}
        expanded={expanded}
        onChange={onToggleExpansion}
      >
        <ExpansionPanelSummary>
          <Grid container spacing={10}>
            <Grid item xs={3} sm={4}>
              <Typography>{formatDate(transaction.issued_date)}</Typography>
              {expanded ? null : (
                <CategoryInfoField
                  user_id={user_id}
                  transaction={transaction}
                  category={getCategory()}
                  onAccept={onAcceptSmartCat}
                />
              )}
            </Grid>
            <Grid item xs={6} sm={4}>
              {transaction.origin_payload ? (
                <Tooltip
                  title={
                    <JSONTree shouldExpandNode={() => true} data={transaction.origin_payload} />
                  }
                >
                  {transaction.origin_payload['anchor_account_name'] ? (
                    <Icon style={{ color: 'blue', float: 'left' }} className="material-icons">
                      waves
                    </Icon>
                  ) : (
                    <PlaidIcon />
                  )}
                </Tooltip>
              ) : null}
              <Typography variant="body1">{transaction.merchant}</Typography>
              <div>
                <Typography color="textSecondary">{transaction.account_name}</Typography>
                <Typography color="textSecondary">{transaction.plaid_category}</Typography>
                <Typography color="textSecondary">{transaction.notes}</Typography>
              </div>
            </Grid>
            <Grid item xs={3} sm={4} style={{ paddingLeft: '40px' }}>
              <Typography>{`$${transaction.total_amount}`}</Typography>
              {+transaction.balance_adjust && !expanded ? (
                <Typography
                  style={{
                    color: +transaction.balance_adjust > 0 ? 'green' : 'red',
                  }}
                >
                  {`$${transaction.balance_adjust}`}
                </Typography>
              ) : null}
            </Grid>
          </Grid>
        </ExpansionPanelSummary>
        <ExpansionPanelDetails>
          <Grid container spacing={10}>
            <Grid item xs={6} sm={4}>
              <DebouncedTextField
                helperText="notes"
                setter={() => {
                  // do nothing
                }}
                onBounce={newValue =>
                  butler.UpdateTransaction({ id: transaction.id, notes: newValue }, 'merge-append')
                }
              />
            </Grid>
            <Grid item xs={6} sm={4}>
              <CategoryField
                {...{
                  transaction,
                  selectableCategories,
                  butler,
                }}
              />
            </Grid>
            <Grid item xs={6} sm={3}>
              <DebouncedTextField
                type="number"
                helperText="Balance Adjust"
                value={balanceAdjust}
                setter={setBalanceAdjust}
                onBounce={newValue => {
                  butler.UpdateTransaction(
                    {
                      id: transaction.id,
                      balance_adjust: newValue,
                    },
                    'merge-append',
                    () => butler.GetUserBalance({}, 'replace'),
                  )
                }}
              />
            </Grid>
            <Grid item xs={6} sm={1} style={{ textAlign: 'right' }}>
              <Fab
                size="medium"
                color="secondary"
                aria-label="Delete"
                onClick={() =>
                  butler.ArchiveTransaction(
                    {
                      id: transaction.id,
                    },
                    'remove',
                  )
                }
              >
                <Delete />
              </Fab>
            </Grid>
          </Grid>
        </ExpansionPanelDetails>
      </ExpansionPanel>
    </div>
  )
}
const areEqual = (last: ITransactionRowProps, next: ITransactionRowProps) => {
  if (
    _.isEqual(last.transaction, next.transaction) &&
    last.expanded === next.expanded &&
    last.selectableCategories.length === next.selectableCategories.length
  ) {
    return true
  }
  return false
}
export const TransactionRow = connectButler(state => {
  return {
    selectableCategories: getSelectableCategories(state),
    user_id: state.user_id,
  }
})(React.memo(TransactionRowComponent, areEqual))
