import * as React from 'react'
import * as _ from 'lodash'
import { TextField } from '@material-ui/core'

interface IDebouncedTextField {
  helperText?: string
  onBounce: (newValue) => void
  setter: (newValue) => void
  type?: string
  value?: any
}

export const DebouncedTextField: React.FunctionComponent<IDebouncedTextField> = ({
  helperText,
  setter,
  onBounce,
  type,
  value,
}) => {
  const debounced = React.useCallback(_.debounce(onBounce, 1000), [])

  return (
    <TextField
      helperText={helperText}
      type={type}
      onChange={event => {
        setter(event.target.value)
        debounced(event.target.value)
      }}
      value={value}
    />
  )
}
