export const CategoryListPage: React.FC<{}> = props => {
  const butler = React.useContext(ButlerContext)
  const [categories, setCategories] = React.useState<Array<ICategory>>([])
  React.useEffect(() => {
    if (!categories.length) {
      butler.ListCategories({}, 'replace', data => {
        setCategories(data.categories)
      })
    }
  })
  return (
    <div>
      <Tree root={{ id: '1', children: [] }} />
      <div>
        {categories.map(cat => (
          <CategoryCard key={cat.id} category={cat} />
        ))}
      </div>
    </div>
  )
}
