import { ICheckPoint, EventType, IStatement, IImportBatch, IEvent } from '@src/types'

export const getEventDescription = (e: IEvent) => {
  if (e.event_type === EventType.CHECKPOINT) {
    const checkpoint = e as ICheckPoint
    return checkpoint.signed_ts
      ? `balance: ${checkpoint.signed_balance}  Signed at ${new Date(
          checkpoint.signed_ts,
        ).toLocaleDateString()}`
      : 'Not Signed'
  }
  if (e.event_type === EventType.BATCH_IMPORT) {
    const batch_import = e as IImportBatch
    return `${batch_import.origin}: ${batch_import.start_date} TO ${batch_import.end_date}`
  }
  if (e.event_type === EventType.STATEMENT_UPLOAD) {
    const upload = e as IStatement
    return `${upload.source}: ${upload.first_date} TO ${upload.last_date}`
  }
  throw new Error('not possible')
}
export const getEventIcon = (event: IEvent) => {
  if (event.event_type === EventType.CHECKPOINT) {
    return 'toll'
  }
  if (event.event_type === EventType.BATCH_IMPORT) {
    const batch_import = event as IImportBatch
    return { plaid: 'grid_on', wave: 'waves' }[batch_import.origin]
  }
  if (event.event_type === EventType.STATEMENT_UPLOAD) {
    // const upload = memory as IStatement
    return 'cloud_upload'
  }
  throw new Error('not possible')
}

export const getEventButtonIcon = (event: IEvent) => {
  if (event.event_type === EventType.CHECKPOINT) {
    return 'plus_one'
  }
  return null
}
