import { createBrowserHistory } from 'history'
import { UrlPathEnum } from '@src/types'

export const history = createBrowserHistory()
export const pushHistory = (
  pathname: UrlPathEnum,
  urlState?: {} | null,
  state: {} | null = null,
) => {
  const searchString =
    urlState && urlState && Object.keys(urlState).length
      ? '?urlState=' + JSON.stringify(urlState)
      : ''
  return history.push({
    pathname,
    search: searchString,
    state,
  })
}

export const getPathName = () => {
  const path = history.location.pathname
  return path.endsWith('/') ? path.slice(0, path.length - 1) || '/' : path
}
