import { Button, Dialog, DialogContent, DialogTitle, Icon } from '@material-ui/core'
import _ from 'lodash'
import * as React from 'react'
import { Autocomplete } from 'rice-instructor'

interface Prop {
  instancePropNameToUpdate: string
  choices: Array<{
    value: string | number
    mui_icon_id?: string
    label: string
  }>
  instance: {}
  onChange: (instance, changes) => void
  style?: any
  defaultValue?: string
}

export class SingleSelectField extends React.Component<Prop, any> {
  constructor(props) {
    super(props)
    this.state = {
      openDialog: false,
    }
  }

  getInitialOption = () => {
    const value = _.get(this.props.instance, this.props.instancePropNameToUpdate, null)

    if (!value) return null
    const option = this.props.choices.find(x => x.value === value)
    if (!option) {
      throw new Error(`${value} not found `)
    }

    return option
  }

  openDialog = e => {
    this.setState({ openDialog: true })
  }
  closeDialog = () => {
    this.setState({ openDialog: false })
  }
  onSelectOption = selectedOption => {
    const { onChange, instance, instancePropNameToUpdate } = this.props
    onChange(instance, {
      [instancePropNameToUpdate]: selectedOption.value,
    })
    this.closeDialog()
  }

  render() {
    const { choices } = this.props
    const initialOption = this.getInitialOption()
    return (
      <div>
        <Button onClick={this.openDialog} variant="outlined" color="primary">
          {initialOption ? (
            <Icon style={{ marginRight: '5px' }}>{initialOption.mui_icon_id}</Icon>
          ) : null}
          {initialOption ? initialOption.label : 'Select A Category'}
        </Button>
        <Dialog open={this.state.openDialog} fullWidth onClose={this.closeDialog}>
          <DialogTitle>Select a category</DialogTitle>
          <DialogContent>
            <Autocomplete
              onSelectOption={this.onSelectOption}
              defaultValue={initialOption ? initialOption.value : undefined}
              placeholder="Select a category"
              options={choices}
            />
          </DialogContent>
        </Dialog>
      </div>
    )
  }
}
