import * as React from 'react'

import { Grid, IconButton } from '@material-ui/core'
import { Check, Close } from '@material-ui/icons'
import { IPrediction, CreateTransaction__Request } from '../types/index'
import { SingleSelectField } from '@src/components/SingleSelectField'
import { ICategory, BUTLER_ACTIONS, IUserTransaction } from '@src/__generated__/backendSchemas'

interface CategoryFieldProp {
  transaction: IUserTransaction | CreateTransaction__Request
  butler: typeof BUTLER_ACTIONS
  selectableCategories: Array<ICategory>
}

interface SmartCatProp {
  prediction: IPrediction
  onAccept: () => void
}
export class SmartCatField extends React.Component<SmartCatProp, any> {
  render() {
    const { prediction, onAccept } = this.props
    const split = prediction.split ? 'Split ' : ''
    return (
      <div style={{ color: 'rgb(0, 188, 212)', fontWeight: 'bold' }}>
        {`${split}${prediction.category_name}?`}
        <Grid container>
          <Grid item xs={12} sm={1}>
            <IconButton onClick={() => onAccept()}>
              <Check color="primary" />
            </IconButton>
          </Grid>
          <Grid item xs={12} sm={1}>
            <IconButton onClick={() => this.setState({ declined: true })}>
              <Close color="secondary" />
            </IconButton>
          </Grid>
        </Grid>
      </div>
    )
  }
}

export class CategoryField extends React.Component<CategoryFieldProp, any> {
  constructor(props) {
    super(props)
    this.state = {
      declined: false,
    }
  }

  onSelectCategory(transaction, changes) {
    return this.props.butler.UpdateTransaction(
      {
        id: transaction.id,
        category_id: changes.category_id,
      },
      'merge-append',
    )
  }

  render() {
    const { selectableCategories, transaction } = this.props
    return (
      <SingleSelectField
        instance={transaction}
        choices={selectableCategories.map(cat => {
          return {
            value: cat.id,
            mui_icon_id: cat.mui_icon_id,
            label: cat.description,
          }
        })}
        instancePropNameToUpdate="category_id"
        onChange={this.onSelectCategory.bind(this)}
      />
    )
  }
}
