import * as React from 'react'
import {
  ListItemSecondaryAction,
  ButtonGroup,
  Button,
  Tooltip,
  Icon,
  ListItemIcon,
  ListItem,
  ListItemText,
  Typography,
} from '@material-ui/core'
import { getEventButtonIcon, getEventIcon, getEventDescription } from './utils'
import { BASE_URL } from '@src/actions'
import { ButlerContext, UrlStateContext } from '@src/Root/contexts'
import { EventType, ICheckPoint, IEvent, UrlPathEnum } from '@src/types'
import { pushHistory } from '@src/history'

export const EventRow = (props: { authToken: string; event: IEvent }) => {
  const urlState = React.useContext(UrlStateContext)
  const butler = React.useContext(ButlerContext)

  const { event } = props
  const disabled = urlState.event_id === `${event.event_type}-${event.id}`
  if (event.event_type === EventType.CHECKPOINT) {
    return (
      <CheckpointListItem
        authToken={props.authToken}
        disabled={disabled}
        checkpoint={event as ICheckPoint}
      />
    )
  }
  const eventIcon = getEventIcon(event)
  const onFilter = e => {
    butler.ListTransactions(
      { start: 0, end: 40, event_id: `${event.event_type}-${event.id}` },
      'replace',
      data =>
        pushHistory(UrlPathEnum.TransactionList, {
          event_id: `${event.event_type}-${event.id}`,
        }),
    )
  }
  const eventButtonIcon = getEventButtonIcon(event)
  return (
    <ListItem selected={disabled} disabled={disabled} button onClick={onFilter}>
      <ListItemIcon>
        <Tooltip title={event.event_type}>
          <Icon color="primary">{eventIcon}</Icon>
        </Tooltip>
      </ListItemIcon>
      <ListItemText>
        <Typography>{`${new Date(event.created_ts).toLocaleDateString()}`}</Typography>
        <Typography color="textSecondary">{getEventDescription(event)}</Typography>
      </ListItemText>
      {eventButtonIcon ? (
        <ListItemSecondaryAction>
          <Button>
            <Icon>{eventButtonIcon}</Icon>
          </Button>
        </ListItemSecondaryAction>
      ) : null}
    </ListItem>
  )
}

export const CheckpointListItem: React.FC<{
  authToken: string
  disabled: boolean
  checkpoint: ICheckPoint
}> = ({ authToken, checkpoint, disabled }) => {
  const butler = React.useContext(ButlerContext)
  const onFilter = e => {
    butler.ListTransactions(
      { start: 0, end: 40, event_id: `${checkpoint.event_type}-${checkpoint.id}` },
      'replace',
      data =>
        pushHistory(UrlPathEnum.TransactionList, {
          event_id: `${checkpoint.event_type}-${checkpoint.id}`,
        }),
    )
  }
  const description = checkpoint.signed_ts
    ? `balance: ${checkpoint.signed_balance}  Signed at ${new Date(
        checkpoint.signed_ts,
      ).toLocaleDateString()}`
    : 'Not Signed'
  return (
    <ListItem selected={disabled} disabled={disabled} button onClick={onFilter}>
      <ListItemIcon>
        <Tooltip title={checkpoint.event_type}>
          {checkpoint.signed_ts ? (
            <Icon color="primary">toll</Icon>
          ) : (
            <Icon style={{ color: 'green' }}>fiber_new</Icon>
          )}
        </Tooltip>
      </ListItemIcon>
      <ListItemText>
        <Typography>{`${new Date(checkpoint.created_ts).toLocaleDateString()}`}</Typography>
        <Typography color="textSecondary">{description}</Typography>
      </ListItemText>
      <ListItemSecondaryAction>
        <ButtonGroup
          color="secondary"
          variant="contained"
          size="small"
          aria-label="Small outlined button group"
        >
          {checkpoint.signed_ts ? null : (
            <Button
              size="small"
              color="secondary"
              onClick={() => butler.SignCheckPoint({ id: checkpoint.id }, 'merge-append')}
            >
              <Icon>plus_one</Icon>
            </Button>
          )}
          <Button
            size="small"
            color="secondary"
            disabled={!authToken}
            onClick={() => {
              window.open(`${BASE_URL}/export-checkpoint/${authToken}/${checkpoint.id}/`, '_blank')
            }}
          >
            <Icon>cloud_download</Icon>
          </Button>
        </ButtonGroup>
      </ListItemSecondaryAction>
    </ListItem>
  )
}
