import * as _ from 'lodash'
import { Button, Grid } from '@material-ui/core'
import * as React from 'react'
import { getSelectableCategories, getVisibleTransactions } from '../selectors'

import { NewPlaidForm } from './NewPlaidForm'
import { NewStatementForm } from './NewStatementForm'
import { NewTransactionForm } from './NewTransactionForm'
import { TransactionListAppBar } from './TransactionListAppBar'
import { TransactionRow } from './TransactionRow'
import { IUserTransaction, ICategory } from '@src/__generated__/backendSchemas'
import { Timeline } from '@src/Root/Timeline'
import { ButlerProps, connectButler } from '@src/utils'
import { State as AppState } from '@src/types'

interface PropsFromStore {
  transactions: Array<IUserTransaction>
  selectableCategories: Array<ICategory>
}
const PAGE_SIZE = 40

type Prop = ButlerProps & PropsFromStore

type FormType = 'transaction' | 'plaid' | 'statement' | 'wave'

const TransactionListContainer = (props: Prop) => {
  console.log('rerender list')
  const [showTimeline, setShowTimeline] = React.useState(true)
  const [formType, setFormType] = React.useState<FormType | null>(null)
  const [expanded, setExpanded] = React.useState<number | null>(null)
  const loadMoreTransactions = () => {
    const start = props.transactions.length
    const end = start + PAGE_SIZE
    props.butler.ListTransactions({ start, end }, 'merge-append')
  }

  React.useEffect(() => {
    if (!props.selectableCategories.length) {
      props.butler.ListCategories({}, 'replace')
      loadMoreTransactions()
    }
  })

  const showForm = (formType: FormType) => {
    setFormType(formType)
  }
  return (
    <div style={{ maxWidth: '100%' }}>
      <TransactionListAppBar toggleTimeline={setShowTimeline} showForm={showForm} />
      <div
        style={{
          height: window.innerHeight - 70,
          overflow: 'scroll',
        }}
      >
        <Grid container>
          {showTimeline ? (
            <Grid item sm={3}>
              <Timeline />
            </Grid>
          ) : null}
          <Grid item sm={showTimeline ? 9 : 12}>
            <div style={{ marginBottom: '20px' }}>
              {formType === 'transaction' ? (
                <NewTransactionForm onClose={() => setFormType(null)} />
              ) : null}
              {formType === 'statement' ? (
                <NewStatementForm onClose={() => setFormType(null)} />
              ) : null}
              {formType === 'plaid' ? <NewPlaidForm origin="plaid" /> : null}
              {formType === 'wave' ? <NewPlaidForm origin="wave" /> : null}
            </div>
            {props.transactions.map(t => (
              <TransactionRow
                expanded={expanded === t.id}
                key={t.id}
                transaction={t}
                onToggleExpansion={(event, expanded) => setExpanded(t.id)}
              />
            ))}
            <Button fullWidth size="large" color="secondary" onClick={loadMoreTransactions}>
              ({props.transactions.length}) Click to Load More ...
            </Button>
          </Grid>
        </Grid>
      </div>
    </div>
  )
}

const mapStateToProps = (state: AppState): PropsFromStore => {
  return {
    transactions: getVisibleTransactions(state),
    selectableCategories: getSelectableCategories(state),
  }
}

export const TransactionList = connectButler(mapStateToProps)(
  React.memo(TransactionListContainer, (last: Prop, next: Prop) => {
    if (
      last.selectableCategories.length === next.selectableCategories.length &&
      _.isEqual(last.transactions, next.transactions)
    ) {
      return true
    }
    return false
  }),
)
