import * as React from 'react'
import * as _ from 'lodash'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { InputAdornment, Menu, MenuItem, TextField } from '@material-ui/core'
import { Search } from '@material-ui/icons'
import { pushHistory } from '@src/history'
import { UrlPathEnum, BUTLER_ACTIONS, State } from '@src/types'

interface PropsFromStore {
  suggestions: Array<any>
}
interface DispatchProps {
  butler: typeof BUTLER_ACTIONS
}
type Props = PropsFromStore & DispatchProps

class SearchBarComponent extends React.Component<Props, { anchorEl: JSX.Element | null }> {
  state = { anchorEl: null }

  debounced = _.debounce(
    newValue =>
      this.props.butler.ListTransactions({ search_text: newValue }, 'replace', data =>
        pushHistory(UrlPathEnum.TransactionList, {
          search_text: newValue,
        }),
      ),
    500,
  )

  onFocus = event => {
    this.setState({ anchorEl: event.currentTarget })
  }
  handleClose = event => {
    this.setState({ anchorEl: null })
  }
  onSearch = event => {
    this.debounced(event.target.value)
  }
  render() {
    const icon = (
      <InputAdornment position="start">
        <Search />
      </InputAdornment>
    )
    return (
      <div>
        <TextField
          onChange={this.onSearch}
          InputProps={{
            startAdornment: <InputAdornment position="start">{icon}</InputAdornment>,
          }}
        />
        <Menu
          open={!!this.state.anchorEl}
          anchorEl={this.state.anchorEl}
          onClose={this.handleClose}
          getContentAnchorEl={null}
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
          }}
        >
          {this.props.suggestions.map((sug, index) => (
            <MenuItem
              key={index}
              onClick={() => {
                this.props.butler.ListTransactions({}, 'replace')
              }}
            >
              {sug.label}
            </MenuItem>
          ))}
        </Menu>
      </div>
    )
  }
}

const mapStateToProps = (state: State, ownProps): PropsFromStore => {
  return { suggestions: [] }
}

const mapDispatchToProps = dispatch => {
  return {
    butler: bindActionCreators(BUTLER_ACTIONS, dispatch),
  }
}
export const SearchBar = connect<PropsFromStore, DispatchProps>(
  mapStateToProps,
  mapDispatchToProps,
)(SearchBarComponent)
