export function addSnack(message: string) {
  return {
    type: 'ADD_SNACK',
    snack: message,
  }
}

export function eatSnack() {
  return {
    type: 'EAT_SNACK',
  }
}

export const BASE_URL = `${window.location.protocol}//${window.location.hostname}:${
  window.location.port ? '8000' : ''
}`
export const BASE_API_URL = `${BASE_URL}/api2`

export function callButlerApi(request: { endpoint: string; jsonBody: {}; authToken: string }) {
  const url = `${BASE_API_URL}/${request.endpoint}/`
  const jwt = request.authToken

  return fetch(url, {
    method: 'POST',
    body: JSON.stringify(request.jsonBody),
    // credentials: 'include',
    headers: {
      'Content-Type': 'application/json',
      'X-Requested-With': 'XMLHttpRequest',
      Authorization: jwt ? `JWT ${jwt}` : '',
    },
  })
}

export const ACTIONS = {
  eatSnack,
  addSnack,
}
