import { Button, Grid, TextField } from '@material-ui/core'
import * as React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { BUTLER_ACTIONS } from '@src/__generated__/backendSchemas'

interface DispatchProps {
  butler: typeof BUTLER_ACTIONS
}

interface OwnProps {
  origin: 'plaid' | 'wave'
}

type FinalProps = OwnProps & DispatchProps
const today = new Date().toISOString().slice(0, 10)

export class NewPlaidFormComponent extends React.Component<
  FinalProps,
  { start_date: string; end_date: string }
> {
  state = { start_date: today, end_date: today }

  importTransactions = () => {
    if (this.props.origin === 'plaid') {
      this.props.butler.ImportFromPlaid(
        {
          start_date: this.state.start_date,
          end_date: this.state.end_date,
        },
        'merge-append',
      )
    } else {
      this.props.butler.ImportFromWave(
        {
          start_date: this.state.start_date,
          end_date: this.state.end_date,
        },
        'merge-append',
      )
    }
  }

  setStartDate = e => {
    this.setState({ start_date: e.target.value })
  }
  setEndDate = e => {
    this.setState({ end_date: e.target.value })
  }

  render() {
    return (
      <Grid container>
        <Grid item xs={4}>
          <TextField
            type="date"
            label="from"
            value={this.state.start_date}
            onChange={this.setStartDate}
          />
        </Grid>
        <Grid item xs={4}>
          <TextField type="date" label="to" defaultValue={today} onChange={this.setEndDate} />
        </Grid>
        <Grid item xs={4}>
          <Button
            onClick={this.importTransactions}
            style={{ marginTop: '10px' }}
            color="primary"
            variant="contained"
          >
            Import
          </Button>
        </Grid>
      </Grid>
    )
  }
}
const mapDispatchToProps = dispatch => {
  return {
    butler: bindActionCreators(BUTLER_ACTIONS, dispatch),
  }
}
export const NewPlaidForm = connect<{}, DispatchProps>(
  null,
  mapDispatchToProps,
)(NewPlaidFormComponent)
