import { List } from '@material-ui/core'
import * as React from 'react'
import { EventRow } from './EventRow'
import { connectButler } from '@src/utils'
import { ButlerContext } from '@src/Root/contexts'
import { IEvent } from '@src/types'
import { getAuthToken } from '@src/selectors'

const TimelineComponent = (props: { events: Array<IEvent>; authToken: string }) => {
  const butler = React.useContext(ButlerContext)
  React.useEffect(() => {
    butler.ListEvents({}, 'replace')
  }, [])
  return (
    <div>
      <List>
        {props.events.map((e, index) => {
          return <EventRow authToken={props.authToken} event={e} key={index} />
        })}
      </List>
    </div>
  )
}
export const Timeline = connectButler(state => {
  return {
    events: state.events,
    authToken: getAuthToken(state),
  }
})(TimelineComponent)
