import { List, ListItem, ListItemText } from '@material-ui/core'
import { Route, Switch } from 'react-router'
import { bindActionCreators } from 'redux'
import * as React from 'react'
import { hot } from 'react-hot-loader'
import { connect } from 'react-redux'
import { CategoryListPage } from './CategoryListPage'
import { StateContext, UrlStateContext, ButlerContext } from './contexts'
import { BUTLER_ACTIONS, State, UrlPathEnum } from '@src/types'
import * as C from '@src/common'
import { getPathName, pushHistory } from '@src/history'
import { LoginPage } from '@src/LoginPage'
import { TransactionChart } from '@src/TransactionChart'
import { parseUrlState } from '@src/selectors'
import { TransactionList } from '@src/TransactionList'

interface StateProps {
  isAuthenticated: boolean
  urlState: any
  state: State
}
interface DispatchProps {
  butler: typeof BUTLER_ACTIONS
}

type Prop = StateProps & DispatchProps

class RootComponent extends React.Component<Prop, State> {
  render() {
    return (
      <C.FlexContainer>
        <div style={{ maxWidth: '100%', flex: 8 }}>
          <StateContext.Provider value={this.props.state}>
            <ButlerContext.Provider value={this.props.butler}>
              <UrlStateContext.Provider value={this.props.urlState}>
                <Body isAuthenticated={this.props.isAuthenticated} />
              </UrlStateContext.Provider>
            </ButlerContext.Provider>
          </StateContext.Provider>
        </div>
      </C.FlexContainer>
    )
  }
}

export const Root = connect<StateProps, DispatchProps>(
  (state: State, ownProps) => {
    return {
      isAuthenticated: !!(state.authToken || sessionStorage.getItem('authToken')),
      urlState: parseUrlState(),
      state,
    }
  },
  dispatch => ({
    butler: bindActionCreators(BUTLER_ACTIONS, dispatch),
  }),
)(RootComponent)

const PageList: React.FC = () => {
  const UrlPathsMap = {
    TransactionList,
    TransactionChart,
    CategoryListPage,
  }
  return (
    <List>
      {Object.keys(UrlPathsMap).map((c, index) => {
        return (
          <ListItem key={index} button onClick={() => pushHistory(c as UrlPathEnum)}>
            <ListItemText primary={c} />
          </ListItem>
        )
      })}
    </List>
  )
}

class Body extends React.Component<{ isAuthenticated: boolean }> {
  render() {
    if (getPathName() === UrlPathEnum.LoginPage) {
      return <LoginPage />
    }
    if (!this.props.isAuthenticated) {
      console.warn('not authenticated!!!')
      pushHistory(UrlPathEnum.LoginPage)
      return <div />
    }
    if (getPathName() === UrlPathEnum.Root) {
      return <PageList />
    }
    if (getPathName() === UrlPathEnum.TransactionList) {
      return <TransactionList />
    }
    if (getPathName() === UrlPathEnum.TransactionChart) {
      return <TransactionChart />
    }
    if (getPathName() === UrlPathEnum.CategoryListPage) {
      return <CategoryListPage />
    }

    throw new Error(`Invalid paths: ${getPathName()}`)
  }
}
export const App = hot(module)(() => (
  <Switch>
    <Route path="/" component={Root} />
  </Switch>
))
