import {
  AppBar,
  Icon,
  IconButton,
  ListItemIcon,
  ListItemText,
  MenuItem,
  Toolbar,
  Tooltip,
  Typography,
} from '@material-ui/core'
import { Add, AttachMoney, CreditCard, GridOn, History, Waves } from '@material-ui/icons'
import * as React from 'react'
import { IconMenu } from 'rice-instructor'
import { State as AppState } from '@src/types'
import { connectButler } from '@src/utils'
import { ButlerContext } from '@src/Root/contexts'
import { SearchBar } from '@src/Root/SearchBar'

interface Prop {
  balance: string | null
  showForm: (formType: string) => void
  toggleTimeline: () => void
}

const TransactionListAppBarComponent = (props: Prop) => {
  const showNewTransactionForm = () => {
    props.showForm('transaction')
  }

  const showNewStatementForm = () => {
    props.showForm('statement')
  }

  const showPlaidImportForm = () => {
    props.showForm('plaid')
  }
  const showWaveImportForm = () => {
    props.showForm('wave')
  }

  const toggleTimeline = () => {
    props.toggleTimeline()
  }

  const butler = React.useContext(ButlerContext)
  React.useEffect(() => {
    if (!props.balance) {
      butler.GetUserBalance({}, 'replace')
    }
  })
  return (
    <AppBar position="static" color="default">
      <Toolbar>
        <Tooltip title="Timeline">
          <IconButton onClick={toggleTimeline}>
            <History />
          </IconButton>
        </Tooltip>
        {props.balance ? (
          <Typography variant="body1" key="balance">{`Balance: $${props.balance}`}</Typography>
        ) : null}
        <div style={{ flex: 1 }} />
        <SearchBar />
        <IconMenu icon={<Add />}>
          <MenuItem key="new-transaction-menu" onClick={showNewTransactionForm}>
            <ListItemIcon>
              <AttachMoney />
            </ListItemIcon>
            <ListItemText>Transaction</ListItemText>
          </MenuItem>
          <MenuItem key="new-statement-menu" onClick={showNewStatementForm}>
            <ListItemIcon>
              <CreditCard />
            </ListItemIcon>
            <ListItemText>Statement</ListItemText>
          </MenuItem>
          <MenuItem key="new-wave-menu" onClick={showWaveImportForm}>
            <ListItemIcon>
              <Waves color="primary" />
            </ListItemIcon>
            <ListItemText>Wave Import</ListItemText>
          </MenuItem>
          <MenuItem key="new-plaid-menu" onClick={showPlaidImportForm}>
            <ListItemIcon>
              <GridOn color="primary" />
            </ListItemIcon>
            <ListItemText>Plaid Import</ListItemText>
          </MenuItem>
        </IconMenu>
        <IconButton
          onClick={() => {
            sessionStorage.removeItem('authToken')
            window.location.href = '/'
          }}
        >
          <Icon className="material-icons">exit_to_app</Icon>
        </IconButton>
      </Toolbar>
    </AppBar>
  )
}

const mapStateToProps = (state: AppState) => {
  return {
    balance: state.balance,
  }
}
export const TransactionListAppBar = connectButler(mapStateToProps)(TransactionListAppBarComponent)
