import { Middleware } from 'redux'
import { callButlerApi } from './actions'
import { pushHistory } from '@src/history'
import { UrlPathEnum } from '@src/types'
import { EnumRoute } from '@src/__generated__/backendSchemas'

export const apiMiddleware: Middleware = store => next => action => {
  if (action && EnumRoute[action.type]) {
    callButlerApi({
      endpoint: action.route,
      jsonBody: action.data,
      authToken: sessionStorage.getItem('authToken') || store.getState().authToken || '',
    }).then(response => {
      if (response.ok) {
        response.json().then(data => {
          store.dispatch({
            type: `BUTLER_ANSWERED`,
            updateType: action.updateType,
            response: data,
          })
          if (action.successCallback) {
            action.successCallback(data)
          }
        })
      } else if (response.status === 401) {
        sessionStorage.removeItem('jwt')
        pushHistory(UrlPathEnum.LoginPage)
      }
    })
  }

  return next(action)
}
