export const isWide = () => window.screen.availWidth > 800

export const TableRowColumn = props => {
  const style = Object.assign(
    {},
    {
      whiteSpace: 'normal',
      textOverflow: 'initial',
    },
    props.style,
  )
  return <TRC {...props} style={style} />
}

export const FlexContainer = props => {
  const defaultStyle = {
    display: 'flex',
    flexDirection: 'row',
  }
  const newStyle = Object.assign({}, defaultStyle, props.style)
  const newProps = Object.assign({}, props, { style: newStyle })
  return <div {...newProps} />
}
