import {
  Button,
  Divider,
  ExpansionPanel,
  ExpansionPanelActions,
  ExpansionPanelSummary,
  Grid,
  TextField,
} from '@material-ui/core'
import * as React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { getSelectableCategories } from '../selectors'
import { SingleSelectField } from '@src/components/SingleSelectField'
import { BUTLER_ACTIONS, CreateTransaction__Request } from '@src/types'
import { ICategory } from '@src/__generated__/backendSchemas'
import { getToday } from '@src/utils'

interface OwnProps {
  onClose: () => void
}
interface PropsFromStore {
  selectableCategories: Array<ICategory>
}

interface DispatchProps {
  butler: typeof BUTLER_ACTIONS
}
type Prop = OwnProps & PropsFromStore & DispatchProps

interface State {
  transaction: CreateTransaction__Request
}

type FieldName =
  | 'issued_date'
  | 'merchant'
  | 'notes'
  | 'category_id'
  | 'total_amount'
  | 'balance_adjust'

const fieldConfig = {
  issued_date: { type: 'date', required: true },
  merchant: { type: 'text', required: true },
  notes: { type: 'text', required: false },
  category_id: { type: 'number', required: false },
  balance_adjust: { type: 'number', required: false },
  total_amount: { type: 'number', required: true },
}

const FieldInput = (props: {
  fieldName: FieldName
  updateField: (fieldName) => (event) => void
  instance: CreateTransaction__Request
}) => {
  const { fieldName, updateField, instance } = props
  const config = fieldConfig[fieldName]
  let error = false
  if (config.required && !instance[fieldName]) {
    error = true
  } else if (config.required && config.type === 'number' && !instance[fieldName]) {
    error = true
  }

  return (
    <TextField
      type={config.type}
      required={config.required}
      error={error}
      label={fieldName}
      value={instance[fieldName]}
      onChange={updateField(fieldName)}
    />
  )
}
const getInitialState = () => {
  return {
    transaction: {
      issued_date: getToday(),
      merchant: '',
      notes: '',
      category_id: 47,
      total_amount: '0',
      balance_adjust: '0',
    },
  }
}

class NewTransactionFormContainer extends React.Component<Prop, State> {
  state = getInitialState()

  createTransaction = () => {
    this.props.butler.CreateTransaction(this.state.transaction, 'merge-append', data => {
      this.props.onClose()
    })
  }
  updateField = (fieldName: FieldName) => {
    const old = this.state.transaction
    return event => {
      const newState = Object.assign({}, old, {
        [fieldName]: event.target.value,
      })
      this.setState({ transaction: newState })
    }
  }
  isValid() {
    const t = this.state.transaction
    return [t.issued_date, t.merchant, +t.total_amount].every(Boolean)
  }
  render() {
    const { selectableCategories } = this.props
    const transaction = this.state.transaction
    return (
      <div style={{ paddingTop: '10px' }}>
        <ExpansionPanel expanded={true}>
          <ExpansionPanelSummary>
            <Grid container spacing={10}>
              <Grid item xs={6} sm={4}>
                <FieldInput
                  instance={transaction}
                  fieldName="issued_date"
                  updateField={this.updateField}
                />
              </Grid>
              <Grid item xs={6} sm={4}>
                <FieldInput
                  fieldName="merchant"
                  instance={transaction}
                  updateField={this.updateField}
                />
              </Grid>
              <Grid item xs={6} sm={4}>
                <FieldInput
                  fieldName="total_amount"
                  instance={transaction}
                  updateField={this.updateField}
                />
              </Grid>
              <Grid item xs={6} sm={4}>
                <SingleSelectField
                  instance={transaction}
                  choices={selectableCategories.map(cat => {
                    return {
                      value: cat.id,
                      label: cat.description,
                      mui_icon_id: cat.mui_icon_id,
                    }
                  })}
                  instancePropNameToUpdate="category_id"
                  onChange={(instance, changes) => {
                    const newState = Object.assign({}, this.state.transaction, changes)
                    this.setState({ transaction: newState })
                  }}
                />
              </Grid>
              <Grid item xs={6} sm={4}>
                <FieldInput
                  fieldName="notes"
                  instance={transaction}
                  updateField={this.updateField}
                />
              </Grid>
              <Grid item xs={6} sm={4}>
                <FieldInput
                  fieldName="balance_adjust"
                  instance={transaction}
                  updateField={this.updateField}
                />
              </Grid>
            </Grid>
          </ExpansionPanelSummary>
          <Divider />
          <ExpansionPanelActions>
            <Button
              size="small"
              onClick={() => {
                this.setState(getInitialState())
                this.props.onClose()
              }}
            >
              Cancel
            </Button>
            <Button
              size="small"
              color="primary"
              onClick={this.createTransaction}
              disabled={!this.isValid()}
            >
              Save
            </Button>
          </ExpansionPanelActions>
        </ExpansionPanel>
      </div>
    )
  }
}

const mapStateToProps = state => {
  return {
    selectableCategories: getSelectableCategories(state),
    balance: state.balance,
  }
}
const mapDispatchToProps = dispatch => {
  return {
    butler: bindActionCreators(BUTLER_ACTIONS, dispatch),
  }
}
export const NewTransactionForm = connect<PropsFromStore, DispatchProps>(
  mapStateToProps,
  mapDispatchToProps,
)(NewTransactionFormContainer)
