import { MenuItem, Select } from '@material-ui/core'
// import { parseUrlState } from '@src/selectors'
import HighchartsData from 'highcharts-data'
import HighchartsDrilldown from 'highcharts-drilldown'
import * as React from 'react'
import Highchart from 'react-highcharts'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import u from 'updeep'
import { getIsoDateString } from '@src/utils'
import { BUTLER_ACTIONS } from '@src/types'

HighchartsData(Highchart.Highcharts)
HighchartsDrilldown(Highchart.Highcharts)

type ReportType = 'INCOME_EXPENSE' | 'EXPENSE'

const getChartConfig = ({ onClickColumn }) => {
  return {
    chart: {
      type: 'column',
      //height: 800,
      //width: 800
    },
    title: {
      text: 'Income/Expense Report',
    },
    xAxis: {
      type: 'category',
    },
    yAxis: {
      min: 0,
      title: {
        text: 'Spend',
        align: 'high',
      },
      labels: {
        overflow: 'justify',
      },
    },
    tooltip: {
      valueSuffix: ' dollars',
    },
    plotOptions: {
      bar: {
        dataLabels: {
          enabled: true,
        },
      },
      column: {
        dataLabels: {
          enabled: true,
        },
        allowPointSelect: false,
        events: {
          click: onClickColumn,
        },
      },
    },
    legend: {
      enabled: true,
      layout: 'vertical',
      align: 'right',
      verticalAlign: 'top',
      x: -40,
      y: 80,
      floating: true,
      borderWidth: 1,
      shadow: true,
    },
    credits: {
      enabled: false,
    },
  }
}
interface Prop {
  butler: typeof BUTLER_ACTIONS
  initialReportType: string
}

interface State {
  config: any
  reportTitle: string
  chart: any
  report_type: ReportType
}

class TransactionChartComponent extends React.Component<Prop, State> {
  constructor(props) {
    super(props)
    const config = getChartConfig({
      onClickColumn: this.onClickColumn.bind(this),
    })
    this.state = {
      config,
      chart: null,
      reportTitle: '',
      report_type: props.initialReportType,
    }
  }

  onClickColumn(e) {
    if (this.state.report_type === 'INCOME_EXPENSE') {
      return console.log(e)
    }
  }

  getReportTitle(value) {
    return value == 'INCOME_EXPENSE' ? 'Income/Expense Report' : 'Expense Report'
  }

  onChangeReportType = e => {
    this.showReport(e.target.value)
  }

  showReport(report_type: ReportType) {
    const today = new Date()
    const threeMonthAgo = new Date()
    threeMonthAgo.setDate(threeMonthAgo.getDate() - 180)
    const apiName = report_type === 'INCOME_EXPENSE' ? 'GetIncomeExpenseReport' : 'GetExpenseReport'
    this.props.butler[apiName](
      { start_date: getIsoDateString(threeMonthAgo), end_date: getIsoDateString(today) },
      'do-nothing',
      res => {
        const titled = u(
          {
            title: {
              text: this.getReportTitle(report_type),
            },
          },
          this.state.config,
        )
        const config = Object.assign({}, titled, res.chart_data)
        this.setState({ chart: <Highchart config={config} /> })
        this.setState({ report_type, config })
      },
    )
  }

  componentDidMount() {
    this.showReport(this.state.report_type)
  }

  render() {
    if (!this.state.config) {
      return <p>Loading ...</p>
    }
    return (
      <div>
        <Select value={this.state.report_type} onChange={this.onChangeReportType}>
          <MenuItem value={'INCOME_EXPENSE'}>Income/Expense</MenuItem>
          <MenuItem value={'EXPENSE'}>Expense</MenuItem>
        </Select>
        <p>{this.state.reportTitle}</p>
        {this.state.chart}
      </div>
    )
  }
}

const mapDispatchToProps = dispatch => {
  return {
    butler: bindActionCreators(BUTLER_ACTIONS, dispatch),
  }
}
export const TransactionChart = connect(
  null,
  mapDispatchToProps,
)(TransactionChartComponent)
