import * as React from 'react'

import { Checkbox, List, ListItem, ListItemText } from '@material-ui/core'

import { ITreeNode } from '@src/types'

interface TreeProps {
  root: ITreeNode
}
const IdentedLineItemText: React.SFC<{ ident: number }> = props => {
  const { ident, ...itemProps } = props
  if (ident) {
    return <ListItemText {...itemProps} style={{ paddingLeft: `${56 * ident}px` }} />
  }
  return <ListItemText {...itemProps} />
}

function getLevel(node: ITreeNode, level: number) {
  return [
    <ListItem key={node.id}>
      <IdentedLineItemText ident={level}>
        <Checkbox />
        {node.id}
      </IdentedLineItemText>
    </ListItem>,
    ...node.children.map(child => getLevel(child, level + 1)),
  ]
}
export class Tree extends React.Component<TreeProps> {
  render() {
    return (
      <List>{(this.props.root.children.map(child => getLevel(child, 0)) as any).flat(1000)}</List>
    )
  }
}
