import { Button } from '@material-ui/core'
import * as React from 'react'

interface Prop {
  submitFileContent: (content: string | ArrayBuffer | null) => void
  button?: React.ReactElement<any>
}
export class FileUploadButton extends React.Component<Prop, any> {
  fileInput: any

  onFileInputChange = event => {
    const file = event.target.files[0]
    const reader = new FileReader()

    const callback = () => this.props.submitFileContent(reader.result)
    reader.addEventListener('load', callback, false)
    if (file) {
      reader.readAsText(file)
    }
  }

  render() {
    const button = this.props.button || (
      <Button variant="outlined" component="span">
        Upload
      </Button>
    )
    return (
      <div>
        <input
          id="file-upload-form"
          hidden
          ref={ref => (this.fileInput = ref)}
          onChange={this.onFileInputChange}
          type="file"
        />
        <label htmlFor="file-upload-form">{button}</label>
      </div>
    )
  }
}
