import { handleAction } from 'redux-actions'
import * as _ from 'lodash'
import { combineReducers } from 'redux'
import { State } from '@src/types'
import { mergeArrays } from '@src/utils'

export const initialState: State = {
  balance: '',
  user_id: 0,
  events: [],
  categories: [],
  authToken: null,
  isAuthenticated: false,
  snacks: [],
  transactions: [],
}

function createReducer(initialState, handlers) {
  return function reducer(state = initialState, action) {
    if (handlers.hasOwnProperty(action.type)) {
      return handlers[action.type](state, action)
    } else {
      return state
    }
  }
}

const snacks = createReducer(initialState.snacks, {
  EAT_SNACK: snacks => [...snacks.slice(1)],
  ADD_SNACK: (snacks, action) => [...snacks, action.snack],
})

const createButlerReducer = (partialState: keyof State, initialState, sortBy?: (a) => number) => {
  return handleAction(
    `BUTLER_ANSWERED`,
    (state, action: any) => {
      if (!action.response[partialState]) {
        return state
      }
      if (action.updateType === 'replace') {
        return action.response[partialState]
      } else if (action.updateType === 'do-nothing') {
        return state
      } else if (action.updateType === 'remove') {
        return [...state].filter(
          item => !action.response[partialState].find(deleted => deleted.id === item.id),
        )
      } else if (action.updateType === 'merge-append') {
        const ret = mergeArrays(state, action.response[partialState], 'append')
        if (sortBy) {
          return _.sortBy(ret, sortBy)
        }
        return ret
      } else {
        throw new Error('not handled')
      }
    },
    initialState,
  )
}

const reducers = combineReducers({
  snacks,
  transactions: createButlerReducer('transactions', initialState.transactions, transaction => {
    return -1 * transaction.issued_date
  }),
  authToken: createButlerReducer('authToken', initialState.authToken),
  categories: createButlerReducer('categories', initialState.categories),
  events: createButlerReducer('events', initialState.events, e => {
    return -1 * new Date(e.created_ts).getTime()
  }),
  balance: createButlerReducer('balance', initialState.balance),
  user_id: createButlerReducer('user_id', initialState.balance),
})

export default reducers
