import querystring from 'querystring'
import { history } from '@src/history'
import { IUserTransaction } from '@src/__generated__/backendSchemas'
import { State } from '@src/types'

export function getSelectableCategories(state: State) {
  return state.categories.filter(x => x.is_selectable)
}

export function getSnack(state: State) {
  if (!state.snacks.length) {
    return ''
  }
  return state.snacks[0]
}

export function getVisibleTransactions(state): Array<IUserTransaction> {
  return state.transactions || []
}

export function parseUrlState() {
  const params = parseSearch()
  if (params && params.urlState) {
    return JSON.parse(params.urlState as string)
  }
  return {}
}
export function parseSearch() {
  return querystring.parse(history.location.search.split('?')[1])
}

export const getAuthToken = (state: State) =>
  state.authToken || sessionStorage.getItem('authToken') || ''
